.others-card {
  position: relative;
  border-bottom: 1px solid @grey-hover;

  .tablet({
    border: 2px solid @grey-bg;
  });

  &::before,
  &::after {
    position: absolute;
    content: "";
    background-color: @plus-color;
    width: 30px;
    height: 2px;
    left: calc(50% - 15px);
    top: 40px;

    .tablet({
      width: 60px;
      left: calc(50% - 30px);
      top: 82px;
    });
  }

  &::before {
    transform: rotate(90deg);
  }
}

.others-card__wrapper {
  padding: 25px 20px;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  .tablet({
    padding: 38px;
    padding-top: 52px;
    padding-bottom: 34px;
  });

  .desktop({
    padding-left: 35px;
    padding-right: 35px;
    padding-bottom: 40px;
  });
}

.others-card__message {
  display: block;
  font-size: 16px;
  line-height: 1.25;
  font-weight: normal;
  text-align: center;
  text-transform: uppercase;
  margin-top: 54px;
  margin-bottom: 8px;
  color: @other-card;

  .tablet({
    margin-bottom: 23px;
    margin-top: 109px;
    font-size:20px;
    line-height: 1.1;
    width: 65%;
  });

  .desktop({
    width:100%;
    margin-top: 108px;
  });
}

.others-card__note {
  .text();
  display: block;
  font-size: 12px;
  line-height: 1.33;
  text-align: center;
  margin-bottom: 21px;

  .tablet({
    font-size:16px;
    line-height: 1.1;
    width: 70%;
    margin-bottom: 0;
  });

  .desktop({
    width:100%;
  });
}

.others-card__button {
  align-self: stretch;

  .tablet({
    margin-top: auto;
  });
}
