  position: relative;
}
@media (min-width: 768px) {
  .menu__link--index-active::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 2px;
    bottom: -10px;
    left: 0;
    background-color: #68b738;
  }
}
@media (min-width: 1440px) {
  .menu__link--index-active::after {
    background-color: #fff;
  }
}
.menu__button {
  position: absolute;
  top: -41px;
  right: 20px;
  width: 24px;
  height: 18px;
  cursor: pointer;
  background: none;
  border: none;
}
@media (min-width: 768px) {
  .menu__button {
    display: none;
  }
}
.menu--closed .menu__button::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: #000;
  box-shadow: 0 8px 0 0 #000, 0 16px 0 0 #000;
}
.menu--closed .menu__button:active::before {
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 8px 0 0 rgba(0, 0, 0, 0.3), 0 16px 0 0 rgba(0, 0, 0, 0.3);
}
.menu--closed .menu__list {
  display: none;
}
@media (min-width: 768px) {
  .menu--closed .menu__list {
    display: flex;
  }
}
.menu--opened .menu__button::before,
.menu--opened .menu__button::after {
  content: "";
  position: absolute;
  top: calc(50% - 1px);
  left: 0;
  width: 25px;
  height: 2px;
  background-color: #000;
}
.menu--opened .menu__button::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.menu--opened .menu__button::after {
  transform: rotate(-45deg);
}
.menu--opened .menu__button:active::before,
.menu--opened .menu__button:active::after {
  background-color: rgba(0, 0, 0, 0.3);
}
.menu--nojs .menu__button {
  display: none;
}
.menu--nojs .menu__list {
  display: flex;
}
