.title {
  margin: 0;
  padding: 0;
  font-weight: 400;
  color: @black;
}

.title--index-page {
  color: @white;

  .tablet({
    color: @black;
  });
}

.title--green-line {
  position: relative;

  .tablet({
    padding-left: @padding--tablet;
  });

  .desktop({
    padding-left: 74px;
  });

  &::before {
    position: absolute;
    content: "";
    top: 14px;
    left: 0;
    right: 0;
    height: 1px;
    background-color: @green;

    .tablet({
      top: 24px;
    });
  }
}

.title__accent {
  background-color: @white;
  position: relative;
  z-index: 20;
  padding-right: 22px;
  padding-left: @padding--mobile;

  .tablet({
    padding-left: 36px;
    padding-right: 36px;
  });

  &--two-lines {
    display: inline-block;
    max-width: 225px;

    .tablet({
      max-width: unset;
    });
  }
}
